<template>
  <main class="main">
    <TransitionGroup name="list">
      <div class="main__chatitem" v-for="(mess, idx) in message" :key="idx">
        <div class="main__chatitem-body" :class="{
          send: mess.sendId == sendId,
          get: mess.sendId != sendId,
        }">
          <span class="main__chatitem-time"> {{ mess.time }} </span>
          <div class="main__chatitem-text">
            <img :src="mess.photo" alt="" v-if="mess.photo?.length > 0 "/>
            <p>{{ mess.body }}</p>
          </div>
        </div>
      </div>
    </TransitionGroup>
  </main>
</template>

<script>
export default {
  props: {
    message: Array,
    sendId: Number,
  },
};
</script>

